* {
  box-sizing: border-box;
}

body {
  background: #2a2e37;
  font-family: Tektur;
}

button {
  color: inherit;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  background: none;
  border: none;
  padding: 0;
}

.submit-button {
  height: 50px;
  width: 150px;
  color: #00fede;
  background-color: #242628;
  font-size: 140%;
}

.submit-button:focus, .submit-button:hover {
  color: #242628;
  background-color: #757575;
}

.header {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search {
  width: 100px;
  height: 100px;
  background-color: #242628;
  margin: 40px;
  font-family: Tektur;
  transition: all .5s;
  position: relative;
  overflow: hidden;
}

.search:before {
  content: "";
  width: 3px;
  height: 100%;
  background-color: #00fede;
  transition: all .5s;
  display: block;
  position: relative;
}

.search.open {
  width: 420px;
}

.search.open:before {
  height: 60px;
  margin: 20px 0 20px 30px;
  position: absolute;
}

.search-box {
  width: 100%;
  height: 100%;
  box-shadow: none;
  color: #fff;
  background: none;
  border: none;
  padding: 20px 100px 20px 45px;
  font-family: Tektur;
  font-size: 140%;
}

.search-box:focus {
  outline: none;
}

.search-button {
  width: 100px;
  height: 100px;
  cursor: pointer;
  padding: 20px;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}

.search-icon {
  width: 40px;
  height: 40px;
  border: 3px solid #00fede;
  border-radius: 40px;
  margin-left: 5px;
  transition: all .5s;
  display: block;
  position: relative;
}

.search-icon:before {
  content: "";
  width: 3px;
  height: 15px;
  background-color: #00fede;
  transition: all .5s;
  display: block;
  position: absolute;
  top: 30px;
  right: -2px;
  transform: rotate(-45deg);
}

.search-icon:after {
  content: "";
  width: 3px;
  height: 15px;
  background-color: #00fede;
  transition: all .5s;
  display: block;
  position: absolute;
  top: 40px;
  right: -12px;
  transform: rotate(-45deg);
}

.open .search-icon {
  width: 60px;
  height: 60px;
  border-radius: 60px;
  margin: 0;
}

.open .search-icon:before {
  height: 18px;
  top: 23px;
  right: 22px;
  transform: rotate(52deg);
}

.open .search-icon:after {
  height: 18px;
  top: 13px;
  right: 22px;
  transform: rotate(-230deg);
}

